@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600&display=swap);
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  position: relative;
}

body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size:18px;
  background-color:#f1f1f1;

}
h3{
  font-weight: 600;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a{
  text-decoration: none;
  color:inherit;
}
.App {
  text-align: center;

}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.main-container{
  width:80%;
  margin:0 auto;
  max-width: 1200px;
  position: relative;
}
.project-container{
    border: 1px solid rgba(1,1,1,.34);
    border-radius: 5px;
    padding:23px;
    padding-bottom: 10px;
    text-align: left;
     box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
position: relative;
    } 

.project-container:hover{
    box-shadow: 0 10px 20px rgba(0,0,0,0.17), 0 6px 6px rgba(0,0,0,0.18);

}
.project-title{
    text-align: center;
    margin-bottom: 10px;
}
.project-container p{
    margin-bottom: 5px;
    line-height: 1.5;
}
.project-links{
    display: flex;
    justify-content: space-between;
    margin:10px 0;
}
.project-container h5{
    font-size: 20px;
    font-weight: 500;
    padding: 10px 0 ;
}
ul.content{
    list-style: none;
    margin-left: 10px;
    max-width: 100%;
    height: auto;
}
ul.content li{
display: inline-block;
    margin-right: 10px;

}

.project-links a{
    color:rgb(79, 112, 143);
    padding: 7px 10px;
    margin-top: 5px;
    border-radius: 5px;
    border:1px solid rgb(79, 112, 143);
}
.project-links a:hover{
    color:blue;
}

h3.projects{
    margin:20px;
font-weight: 400;
}
.project-grid{
    display:grid;
   grid-template-columns: 1fr;
  grid-row-gap: 10px;
  row-gap: 10px;

}
@media only screen and (min-width: 1000px) {
    .project-grid{
        grid-template-columns: 1fr 1fr;
        grid-column-gap:20px;
        -webkit-column-gap:20px;
                column-gap:20px;
        grid-row-gap:20px;
        row-gap:20px;
    }
  }

.social{
    position:absolute;
    border-top:1px solid rgb(104, 103, 103);
    border-right: 1px solid  rgb(104, 103, 103);
    border-bottom: 1px solid rgb(104, 103, 103);
    padding:5px;
    border-radius: 3px;
    top:10%;
    left:0;
    display: flex;
    flex-direction: column;
    font-size: 35px;
}
.social a{
    color:rgb(44, 44, 46);
}
.social a:hover:nth-child(1){
    color:blue
}
.social a:hover:nth-child(2){
    color:green;
}
.footer{
    height:100px
}
.about-container{
    display: grid;
    grid-template-columns: 1fr;
    margin-top: 10px;
    text-align: left;  
}
.about-container p{
    line-height: 1.5;
}
.about-container h5{
    margin: 0 0 10px 0;
    font-size: 18px;
    font-weight: 500;
}
.about-container p {
    margin-bottom: 5px;
}
.me, .stack-components{
    padding:20px;
}
.about-logos{
   margin: 10px;
   font-size: 40px;
}
.about-logos svg{
    margin-right: 10px;
}
.about-logos svg:nth-child(1){
    color:red
}
.about-logos svg:nth-child(3){
    color:rgb(212, 185, 27);
}
.about-logos svg:nth-child(4){
    color:blue;
}
.about-logos svg:nth-child(5){
    color:green;
}
@media only screen and (min-width: 800px) {

    .about-container{
        grid-template-columns: 1fr 1fr ;
        grid-column-gap: 20px;
        -webkit-column-gap: 20px;
                column-gap: 20px;
        max-width: 1200px;
        margin:0 auto;
        margin-top: 50px;
    }
  }
.contact-container a{
    font-size: 40px;
    margin-right: 10px;
}
.contact-container a:hover{
    color:blue;
}
.contact-container h3{
    margin-top: 20px;
    margin-bottom: 5px;
}
.navbar{
    display: flex;
    width: 100%;
    height:60px;
    align-items: center;
    justify-content: space-around;
    background-color: #cdd6f5;
    color:rgb(24, 24, 24);
    margin-bottom:10px;
}
.logo a{
    font-size: 23px;
    font-weight: 400;

}
.logo a:hover{
    color:purple;
   
}
.links{
    width:30%;
    display: flex;
    justify-content: space-between;
}
.links a{
    font-size: 15px;
    margin-right: 5px;
}
.active-nav{
    border-bottom: 1px solid white;
    padding-bottom: 5px;
}
@media only screen and (min-width: 600px) {
    .links a{
        font-size: inherit;
    }
  }
